import React, { Component } from 'react';
import  Profile from "../../data/profile";
import  './about.css';
import { Button } from 'reactstrap';
class About extends Component {
	render() {
		return (
			<div className="about">
				<ul>
					{
						Profile.map(function(Profile){
							return <li className="text-danger bit" > <p className= "text-success">{Profile.id}  {Profile.firstName} -  {Profile.lastName} </p>-  {Profile.work}  {Profile.url}</li>;
						})
					}
				</ul>
			</div>
		);
	}
}
export default About;


